
import { ActivityMedia } from '@/interfaces/activity';
import { fetchMediaList } from '@/store/lists';
import store, { monthNames } from '@/store/store';
import { defineComponent } from 'vue';

import Popup from '@/components/List/Popup.vue';

interface Year {
    year: number;
    months: {
        count: number;
    }[];
}

export default defineComponent({
    components: { Popup },
    data() {
        return {
            state: store.state,
            loading: false,
            monthNames,

            years: [] as Year[],
            unknownCount: 0,
            max: 0,

            showPopup: false,
            popupMedia: {} as ActivityMedia,

            selected: [] as ActivityMedia[],
            selectedMonth: 0,
            selectedYear: 0,
        }
    },

    created() {
        this.fetchData();
    },

    watch: {
        '$route': 'fetchData'
    },

    computed: {
        list(): ActivityMedia[] {
            const filtered: ActivityMedia[] = (this.$route.name == "AnimeHistory" ? this.state.animeList : this.state.mangaList);

            return filtered;
        }
    },

    methods: {
        fetchData() {
            this.clear();
            this.loading = true;
            if(this.$route.name == "AnimeHistory") {
                if(this.state.animeList.length == 0) {
                    fetchMediaList("ANIME")
                    .then(list => {
                        store.setAnimeList(list);
                        this.parseData();
                    })
                } else {
                    this.parseData();
                }
            } else if (this.$route.name == "MangaHistory") {
                if(this.state.mangaList.length == 0) {
                    fetchMediaList("MANGA")
                    .then(list => {
                        store.setMangaList(list);
                        this.parseData();
                    })
                } else {
                    this.parseData();
                }
            }
        },

        parseData() {
            let currentYear = new Date().getFullYear();

            let progress = true;

            while(progress) {
                const zeros = this.checkYear(currentYear);
                if(zeros < 12) {
                    currentYear--;
                } else {
                    const lesser = this.list.filter(x => x.completed && x.completed?.y < currentYear && x.completed?.y != 0);
                    if(lesser.length == 0) {
                        progress = false;
                        this.years.pop(); // the last, empty year is appended to array of years, so we have to remove it manually here.
                    } else {
                        currentYear--;
                    }
                }

                if(currentYear < 2000) {
                    progress = false;
                }
            }
            
            this.unknownCount = this.list.filter(x => x.status == "COMPLETED" && x.completed?.y == 0).length;

            this.loading = false;
        },
    
        checkYear(year: number): number {
            let zeroCount = 0;
            for (let i = 1; i < 13; i++) {
                const targetYear = this.years.find(m => m.year == year);
                const filtered = this.list.filter(x => x.completed?.m == i && x.completed?.y == year);

                if(filtered.length == 0) zeroCount++;
                if(filtered.length > this.max) this.max = filtered.length;
                if(targetYear) {
                    targetYear.months[i-1] = {
                        count: filtered.length
                    }
                } else {
                    const obj: Year = {
                        year: year,
                        months: []
                    }

                    obj.months[i-1] = {
                        count: filtered.length
                    }

                    this.years.push(obj);
                }
            }

            return zeroCount;
        },

        popupShow(media: ActivityMedia) {
            this.showPopup = true;
            this.popupMedia = media;
        },

        popupHide() {
            this.showPopup = false;
        },

        load(month: number, year: number) {
            const matches = this.list.filter(entry => entry.completed?.m == month && entry.completed?.y == year);
            this.selectedMonth = month;
            this.selectedYear = year;
            this.selected = matches;
        },

        clear() {
            this.years = [];
            this.max = 0;
            this.unknownCount = 0;

            this.selected = [];
            this.selectedMonth = 0;
            this.selectedYear = 0;
        },

        getOpacity(count: number): number {
            const ratio = count / this.max;

            return ratio;
        }
    }
})
