
import { defineComponent } from 'vue';
import SeriesDetails from './SeriesDetails.vue';

export default defineComponent({
    props: {
        media: Object,
        hide: Boolean
    },

    components: { SeriesDetails },

    data() {
        return {
            details: false
        }
    },

    watch: {
        hide: function(val) {
            this.details = !val;
        }
    },

    methods: {
        toggleDisplay(): void {
            this.$emit('toggle');
            this.details = !this.details;
        }
    }

})
